
<template>
	<div class="booklist-wrap" v-loading="loading">
		<div class="search-bar">
			<el-button type="success" size="small" @click="addBook()">申请订购</el-button>
			<el-button type="success" size="small" @click="filter({status:'2'})">全部书籍</el-button>
			<el-input @keyup.enter.native="handleFilter" size="small" placeholder="请输入书籍名" v-model="searchdata">
    			<i slot="prefix" class="el-input__icon el-icon-search"></i>
  			</el-input>
		</div>
		<el-table :data="tableData" style="width: 100%" >
			<el-table-column fixed prop="name" label="名称" width="150"></el-table-column>
			<el-table-column prop="author" label="作者" width="120"></el-table-column>
			<el-table-column prop="category" label="种类" width="120"></el-table-column>
			<el-table-column prop="buyAt" label="购买于" width="120"></el-table-column>
			<el-table-column fixed="right" label="操作">
				<template slot-scope="scope">
					<el-button @click.native.prevent="book(scope.$index, tableData)" type="text" size="small">预约书籍</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :pager-count="8" :page-size="15" layout="total, prev, pager, next, jumper" :total="total">
		</el-pagination>
	</div>
</template>

<script>
import {requestList,createBook,updateBook} from '../../api/book.js'
import {userAddBook} from '../../api/user.js'
import Cookies from 'js-cookie'
export default {
	data() {
		return {
			tableData: [],
			currentPage4:1,
			total:0,
			searchdata:'',
			loading:false,
		}
	},
	created() {
		this.initData({status:'2'})
	},
	methods:{
		initData (option) {
			this.loading = true;
			requestList(option).then( res => {
				this.tableData = res.data.books;
				this.total = res.data.count;
				this.loading = false;

			})
		},
		book(index, tableData) {
			const username = Cookies.get('username');/*用户名需要唯一*/
			var data = Object.assign({},tableData[index],{status:3},{borrower:username});/*细究参数位置的不同最后返回的浅拷贝的结果不同*/
			var userdata = {
				book_id:tableData[index]._id,
				username:username
			}
			updateBook(data).then(res => {
				if(res.data.code === 1){
					this.$message({
						type:'success',
						message:'成功预约，等待图书管理员确认'
					})
					tableData.splice(index,1)
				}
			});

			userAddBook(userdata)/*添加书籍ID到用户文档*/
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(page) {
			this.initData({status:9,page:page})
		},
		addBook () {
			this.$router.push({
				path:'/book/bookadd'
			})
		},
		handleFilter () {
			this.initData({name:this.searchdata,status:8})
		},
		filter (option) {
			this.initData(option)
		}

	}
}
</script>
<style scoped>
.booklist-wrap{
	padding:20px;
}
.search-bar{
	margin-bottom: 20px;
}
.el-pagination{
	float: right;
	margin:20px 0;
}
.el-input{
	width:auto;
}
</style>


