<template>
	<div class="root-header-wrap">
		<div class="root-header">
			<div class="root-header-navs">
				<router-link  v-loading.fullscreen.lock="fullscreenLoading" to="/book" class="root-header-navs-item">图书管理</router-link>
				<router-link  v-loading.fullscreen.lock="fullscreenLoading" to="/goods" class="root-header-navs-item">公司物品管理</router-link>
				<router-link  v-loading.fullscreen.lock="fullscreenLoading" to="/blog" class="root-header-navs-item">公司论坛</router-link>
				<router-link  v-loading.fullscreen.lock="fullscreenLoading" to="/oa" class="root-header-navs-item">员工OA系统</router-link>
			</div>
			<div class="root-header-set">
				<el-dropdown trigger="click" @command="handleCommand">
					<span class="el-dropdown-link rhs-el-dropdown-link">
						{{username}}
						<i class="el-icon-arrow-down el-icon--right"></i>
					</span>
					<el-dropdown-menu slot="dropdown" class="rhs-el-dropdown-menu">
						<el-dropdown-item command="single" class="rhs-el-dropdown-item">个人资料</el-dropdown-item>
						<el-dropdown-item command="message" class="rhs-el-dropdown-item">消息中心</el-dropdown-item>
						<el-dropdown-item command="logout" class="rhs-el-dropdown-item">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
		</div>
	</div>
</template>
<script>
import {logout} from '../../../api/user.js';
import {removeToken} from '../../../utils/auth.js'
import Cookies from 'js-cookie'
export default{
	data () {
		return {
			username:'',
			fullscreenLoading:false,
		}
	},
	mounted () {
		this.username = Cookies.get('username')
	},
	beforeRouteEnter(to,from,next){
		// next(vm => {
		// 	vm.fullscreenLoading = true;
		// })
	},
	computed:{
		// username:function () {
		// 	return this.$store.state.adminInfo
		// }
	},
	methods:{
		handleCommand(command){
			if(command == 'logout'){

				this.$confirm('此操作将退出登录, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					logout().then(res => {
						if(res.data.status == 1){
							removeToken('username');/*清除浏览器cookie*/
							this.$router.push('/login')
							this.$message({
								type: 'success',
								message: '退出成功'
							});
						}else{
							console.log('服务器异常')
						}
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});          
				});

			}else if(command == 'message'){
				console.log('消息中心')
			}else if(command == 'single' ){
				console.log('个人资料')
			}

		}
	}
}
</script>
<style scoped>
.root-header-wrap{
	height: 60px;
	padding:0 100px 0 200px;
	background: #000;

}
.root-header{
	display: flex;
	justify-content: space-between;
}
.root-header-navs{
	font-size: 0;
}
.root-header-navs-item{
	display: inline-block;
	line-height: 60px;
	color: #fff;
	font-size: 16px;
	padding:0 15px;
}
.rhs-el-dropdown-link{
	display: inline-block;
	height: 100%;	
	text-align: center;	
	color: #fff;
	line-height: 60px;
	cursor:pointer;
	font-size: 16px;    
}

.rhs-el-dropdown-menu{
	background: #000 !important;
	border: none !important;
}
.rhs-el-dropdown-menu .popper__arrow{
	border-bottom-color: #000 !important;
}
.rhs-el-dropdown-item{
	font-size: 16px !important;
	color: #fff;
}


</style>


