<template>
	<div class="wrap-container">
		<el-breadcrumb separator="/">
			<el-breadcrumb-item to="/book/booklist">图书列表</el-breadcrumb-item>
			<el-breadcrumb-item>添加书籍</el-breadcrumb-item>
		</el-breadcrumb>
		<div class="el-form-container">
			<el-form ref="bookForm" :model="form" label-width="80px">
				<el-form-item label="书籍名称" prop="name">
					<el-input v-model="form.name"></el-input>
				</el-form-item>
				<el-form-item label="作者" prop="author">
					<el-input v-model="form.author"></el-input>
				</el-form-item>
				<el-form-item label="书籍状态" prop="status">
				    <el-select v-model="form.status" placeholder="书籍状态">
				      <el-option label="预约订购" value="1"></el-option>
				      <el-option label="确认订购" value="2"></el-option>
				      <el-option label="预约书籍" value="3"></el-option>
				      <el-option label="确认借阅" value="4"></el-option>
				    </el-select>
  				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" @click="onSubmit('bookForm')">立即提交</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>
<script>
import {requestList,updateBook,createBook} from '../../api/book.js'
export default {
	data() {
		return {
			form: {}
		}
	},
	methods: {
		onSubmit(formname) {

			if(!this.form.name){
				this.$message({
					type:'warning',
					message:'书籍名称不能为空'
				})
			}else if(!this.form.status){
				this.$message({
					type:'warning',
					message:'状态不能为空'
				})
			}else{
				createBook(this.form).then(res => {
					if(res.data.code == 1){
						this.$message({
							type:'success',
							message:'成功添加书籍'
						})
						this.$refs[formname].resetFields();
					}
				})				
			}
		}
	}
}
</script>
<style scoped>
.el-form-container{
	margin:20px;
	padding:20px;
	background: #fff;
}
.el-breadcrumb{
	margin:20px 0 0 20px;
}
</style>


