

<template>
	<div class="booklist-wrap" v-loading="loading">
		<div class="search-bar">
			<el-button type="success" size="small" @click="filter({status:5})">全部书籍</el-button>
			<el-button type="success" size="small" @click="filter({status:4})">已借阅</el-button>
			<el-button type="success" size="small" @click="filter({status:3})">预约中</el-button>
		</div>
		<el-table :data="tableData" style="width: 100%">
			<el-table-column fixed prop="name" label="名称" width="150"></el-table-column>
			<el-table-column prop="author" label="作者" width="120"></el-table-column>
			<el-table-column prop="category" label="种类" width="120"></el-table-column>
			<el-table-column prop="buyAt" label="购买于" width="120"></el-table-column>
			<el-table-column :formatter="formatStatus" prop="status" label="状态"></el-table-column>
		</el-table>
		<!-- <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :pager-count="8" :page-size="15" layout="total, prev, pager, next, jumper" :total="total">
		</el-pagination> -->
	</div>
</template>

<script>
import {requestList} from '../../api/book.js'
import {userBookList} from '../../api/user.js'
import Cookies from 'js-cookie'
export default {
	data() {
		return {
			tableData: [],
			currentPage4:1,
			total:0,
			loading:false,
		}
	},
	created() {
		const username = Cookies.get('username')
		this.initData({status:'5',username:username})
	},
	methods:{
		initData (option) {
			this.loading = true;
			userBookList(option).then( res => {
				this.tableData = res.data.result;
				this.total = res.data.count;
				this.loading = false;
			})
		},
		formatStatus (row,colume,cellValue,index){
			switch(cellValue){
				case 1:
					return '订购中'
					break;
				case 2:
					return '已订购'
					break;
				case 3:
					return '预约中'
					break;
				case 4:
					return '已借阅'
					break;
				default:
			}
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(page) {
			this.initData({status:5,page:page})
		},
		filter (option) {
			const username = Cookies.get('username')
			let data = Object.assign({},{username:username},option)
			userBookList(data).then( res => {
				this.tableData = res.data.result
			})
		}
	}
}
</script>
<style scoped>
.booklist-wrap{
	padding:20px;
}
.search-bar{
	margin-bottom: 20px;
}
.el-pagination{
	float: right;
	margin:20px 0;
}
</style>


