

<template>
	<div class="table-wrap" v-loading="loading">
		<el-table :data="tableData" style="width: 100%">
			<el-table-column fixed prop="name" label="书籍名" width="150"></el-table-column>
			<el-table-column prop="author" label="作者" width="120"></el-table-column>
			<el-table-column fixed="right" label="操作">
				<template slot-scope="scope">
					<el-button @click.native.prevent="addBook(scope.$index, tableData)" type="text" size="small">确定订购</el-button>
					<el-button @click.native.prevent="delBook(scope.$index, tableData)" type="text" size="small">取消订购</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :pager-count="8" :page-size="15" layout="total, prev, pager, next, jumper" :total="total">
		</el-pagination>
	</div>
</template>

<script>
import {requestList,updateBook,delBook} from '../../api/book.js'
export default {
	data() {
		return {
			tableData: [],
			loading:false,
		}
	},
	created () {
		this.initData({status:1})
	},
	methods:{
		initData (option) {/*初始化数据*/
			this.loading = true;
			requestList(option).then( res => {
				this.tableData = res.data.books;
				this.total = res.data.count;
				this.loading = false;
			})
		},
		handleCurrentChange(page) {
			this.initData({status:1,page:page})
		},
		addBook (index,tableData) {/*确认订购*/
			var data = Object.assign({},tableData[index],{status:2});/*细究参数位置的不同最后返回的浅拷贝的结果不同*/
			updateBook(data).then(res => {
				console.log(res.data)
				if(res.data.code === 1){
					this.$message({
						type:'success',
						message:'订购成功'
					})
					tableData.splice(index,1)
				}
			})
		},
		delBook (index,tableData) {/*取消订购*/
			delBook({_id:tableData[index]._id}).then(res => {
				if(res.data.code === 1){
					this.$message({
						type:'success',
						message:'取消订购'
					})
					tableData.splice(index,1)
				}
			})
		}
	}
}
</script>
<style scoped>
.table-wrap{
	padding:20px;
}
.el-pagination{
	float: right;
	margin:20px 0;
}
</style>


