

<template>
	<div class="booklist-wrap" v-loading="loading">
		<div class="search-bar">
			<el-button type="success" size="small" @click="addBook()">添加管理员</el-button>
			<el-button type="success" size="small" @click="filter({status:'1'})">全部</el-button>
			<el-input @keyup.enter.native="handleFilter" size="small" placeholder="请输入名称" v-model="searchdata">
    			<i slot="prefix" class="el-input__icon el-icon-search"></i>
  			</el-input>
		</div>
		<el-table :data="tableData" style="width: 100%">
			<el-table-column fixed prop="username" label="名称" width="150"></el-table-column>
			<el-table-column prop="createAt" label="创建时间" width="120"></el-table-column>
			<el-table-column fixed="right" label="操作">
				<template slot-scope="scope">
					<el-button @click.native.prevent="book(scope.$index, tableData)" type="text" size="small">编辑</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!-- <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :pager-count="8" :page-size="15" layout="total, prev, pager, next, jumper" :total="total">
		</el-pagination> -->
	</div>
</template>

<script>
import {userList} from '../../api/user.js'
export default {
	data() {
		return {
			tableData: [],
			currentPage4:1,
			total:0,
			searchdata:'',
			loading:false,
		}
	},
	created() {
		this.initData({status:'1',page:1})
	},
	methods:{
		initData (option) {
			this.loading = true;
			userList(option).then( res => {
				this.tableData = res.data.result;
				this.total = res.data.count;
				this.loading = false;
			})
		},
		
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(page) {
			this.initData({status:9,page:page})
		},
		addBook () {
			this.$router.push({
				path:'/book/bookadd'
			})
		},
		handleFilter () {
			this.initData({username:this.searchdata,status:'4'})
		},
		filter(option){
			this.initData(option)
		}

	}
}
</script>
<style scoped>
.booklist-wrap{
	padding:20px;
}
.search-bar{
	margin-bottom: 20px;
}
.el-pagination{
	float: right;
	margin:20px 0;
}
.el-input{
	width:auto;
}
</style>


