<template>
	<div class="wrap-container">
		<el-breadcrumb separator="/">
			<el-breadcrumb-item to="/book/bookcenter">图书中心</el-breadcrumb-item>
			<el-breadcrumb-item>申请订购</el-breadcrumb-item>
		</el-breadcrumb>
		<div class="el-form-container">
			<el-form ref="bookForm" :model="form" label-width="80px">
				<el-form-item label="书籍名称" prop="name">
					<el-input v-model="form.name"></el-input>
				</el-form-item>
				<el-form-item label="作者" prop="author">
					<el-input v-model="form.author"></el-input>
				</el-form-item>

				<el-form-item>
					<el-button type="primary" size="small" @click="onSubmit('bookForm')">立即提交</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>
<script>
import {requestList,updateBook,createBook} from '../../api/book.js'
export default {
	data() {
		return {
			form: {}
		}
	},
	methods: {
		onSubmit(formname) {
			if(!this.form.name){
				this.$message({
					type:'warning',
					message:'书籍名称不能为空'
				})
			}else{
				createBook(this.form).then(res => {
					if(res.data.code == 1){
						this.$message({
							type:'success',
							message:'预订成功，等待图书管理员购买'
						})
						this.$refs[formname].resetFields();
					}
				})				
			}

		}
	}
}
</script>
<style scoped>
.el-form-container{
	margin:20px;
	padding:20px;
	background: #fff;
}
.el-breadcrumb{
	margin:20px 0 0 20px;
}
</style>


