

<template>
	<div class="booklist-wrap" v-loading="loading">
		<div class="search-bar">
			<el-button type="success" size="small" @click="addBook()">添加书籍</el-button>
			<el-button type="success" size="small" @click="filter({status:4})">被借阅</el-button>
			<el-button type="success" size="small" @click="filter({status:2})">未借阅</el-button>
			<el-button type="success" size="small" @click="filter({status:3})">预约中</el-button>
			<el-button type="success" size="small" @click="filter({status:9})">全部书籍</el-button>
			<el-input @keyup.enter.native="handleFilter" size="small" placeholder="请输入书籍名" v-model="searchdata">
    			<i slot="prefix" class="el-input__icon el-icon-search"></i>
  			</el-input>
		</div>
		<el-table :data="tableData" style="width: 100%">
			<el-table-column fixed prop="name" label="名称" width="150"></el-table-column>
			<el-table-column prop="author" label="作者" width="120"></el-table-column>
			<el-table-column prop="category" label="种类" width="120"></el-table-column>
			<el-table-column prop="buyAt" label="购买于" width="120"></el-table-column>
			<el-table-column :formatter="formatStatus" prop="status" label="状态" width="120"></el-table-column>
			<el-table-column fixed="right" label="操作">
				<template slot-scope="scope">
					<el-button @click.native.prevent="editBook(scope.$index, tableData)" type="text" size="small">编辑</el-button>
					<el-button @click.native.prevent="delBook(scope.$index, tableData)" type="text" size="small">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :pager-count="8" :page-size="15" layout="total, prev, pager, next, jumper" :total="total">
		</el-pagination>
	</div>
</template>

<script>
import {requestList,updateBook,delBook} from '../../api/book.js'
export default {
	data() {
		return {
			tableData: [],
			currentPage4:1,
			total:0,
			searchdata:'',
			loading:false,
			loading_table:false,
		}
	},
	created() {
		/*z这里必须传递一个{}空对象去查询status大于2的所有文档*/
		this.initData({status:9,page:1})
	},
	methods:{
		initData (option) {
			this.loading = true;
			requestList(option).then( res => {
				console.log(res.data)
				this.tableData = res.data.books;
				this.total = res.data.count;
				this.loading = false;
			})
		},
		handleFilter () {
			this.initData({name:this.searchdata,status:'7'})
		},
		editBook(index, rows) {
			this.$router.push({
				path:'/book/bookedit',
				query:rows[index],
			})
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(page) {
			this.initData({status:9,page:page})
		},
		addBook () {
			this.$router.push({
				path:'/book/booklistform'
			})
		},
		
		filter(option){
			this.initData(option)
		},
		formatStatus (row,colume,cellValue,index){
			switch(cellValue){
				case 1:
					return '订购中'
					break;
				case 2:
					return '已订购'
					break;
				case 3:
					return '预约中'
					break;
				case 4:
					return '被借阅'
					break;
				default:
			}
		},
		delBook (index,tableData) {/*删除书籍*/

				this.$confirm('此操作将永久删除数据, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					delBook({_id:tableData[index]._id}).then(res => {
						if(res.data.code == 1){
							this.initData({status:8})
							this.$message({
								type: 'success',
								message: '删除成功'
							});
						}else{
							console.log('服务器异常')
						}
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});          
				});

		}
	}
}
</script>
<style scoped>
.booklist-wrap{
	padding:20px;
}
.search-bar{
	margin-bottom: 20px;
}
.el-pagination{
	float: right;
	margin:20px 0;
}
.el-input{
	width:auto;
}
</style>


