<template>
	<div class="wrap-container">
		<el-breadcrumb separator="/">
			<el-breadcrumb-item to="/book/booklist">图书列表</el-breadcrumb-item>
			<el-breadcrumb-item>编辑</el-breadcrumb-item>
		</el-breadcrumb>
		<div class="el-form-container">
			<el-form ref="form" :model="form" label-width="80px">
				<el-form-item label="书籍名称" prop="name">
					<el-input v-model="form.name"></el-input>
				</el-form-item>
				<el-form-item label="作者" prop="author">
					<el-input v-model="form.author"></el-input>
				</el-form-item>
				<el-form-item label="种类" prop="category">
					<el-input v-model="form.category"></el-input>
				</el-form-item>
				<el-form-item label="状态" prop="status">
				    <el-select v-model="form.status" placeholder="书籍状态">
				      <el-option label="预订中" value="1"></el-option>
				      <el-option label="已订购" value="2"></el-option>
				      <el-option label="预约中" value="3"></el-option>
				      <el-option label="已借阅" value="4"></el-option>
				    </el-select>
  				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" @click="onSubmit('form')">立即提交</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>
<script>
import {requestList,updateBook} from '../../api/book.js'
export default {
	data() {
		return {
			form: {}
		}
	},
	beforeRouteEnter ( to,from,next){
		next(vm => {

			let data = vm.formatStatus(to.query,false);
			vm.form = data;
			// console.log(vm.form)
		})
	},
	methods: {
		onSubmit(formname) {
			if(!this.form.name){
				this.$message({
					type:'warning',
					message:'书籍名称不能为空'
				})
			}else if(!this.form.status){
				this.$message({
					type:'warning',
					message:'状态不能为空'
				})
			}else{
				let data = this.formatStatus(this.form,true)/*前台数据转换*/
				updateBook(data).then(res => {
					if(res.data.code == 1){
						this.$router.push('/book/booklist')
						this.$message({
							type:'success',
							message:'修改成功'
						})
					}
				})
			}
			
		},
		formatStatus (data,isToNumber){
			if(isToNumber){
					switch(data.status){
						case '预定中':
							data.status = 1
							return data
							break;
						case '已订购':
							data.status = 2
							return data
							break;
						case '预约中':
							data.status = 3
							return data
							break;
						case '已借阅':
							data.status = 4
							return data
							break;
						default:
							return data;
							break;
				}
			}else{
				console.log('kkkkkkkkk')
				switch(data.status){
						case 1:
							data.status = '预定中'
							return data
							break;
						case 2:
							data.status = '已订购'
							return data
							break;
						case 3:
							data.status = '预约中'
							return data
							break;
						case 4:
							data.status = '已借阅'
							return data
							break;
						default:
			}
			
		}
	}
}
}
</script>
<style scoped>
.el-form-container{
	margin:20px;
	padding:20px;
	background: #fff;
}
.el-breadcrumb{
	margin:20px 0 0 20px;
}
</style>


