import request from '../utils/request.js'


export function requestList (query) {
	return request({
		url:'/api/book/find/?status='+query.status+'&name='+query.name+'&page='+query.page,
		method:'get',
	})
}
export function updateBook (data) {
	return request({
		url:'/api/book/update/'+data._id,
		method:'post',
		data:data || {}
	})
}

export function createBook (data) {
	return request({
		url:'/api/book/add/',
		method:'post',
		data:data
	})
}

export function delBook (data) {
	return request({
		url:'/api/book/del/'+data._id,
		method:'post',
	})
}




