<template>
	<div class="login-wrap">
		<el-form ref="form" :model="form">
		<h3 class="login-title">瑾珑内部系统</h3>
		<el-form-item><el-input v-model="form.username" placeholder='用户名'></el-input></el-form-item>
		<el-form-item><el-input @keyup.enter.native="login" type="password" v-model="form.pwd" placeholder='用户密码'></el-input></el-form-item>
		<el-row><el-button type="primary" @click="login">登陆</el-button></el-row>
		<el-row><el-button type="danger" @click="register">注册</el-button></el-row>
	</el-form>
</div>
</template>
<script>
	import {login} from '../../api/user.js'
	import {removeToken,getToken} from '../../utils/auth.js'
	import Cookies from 'js-cookie';
	export default {
		data() {
			return {
				form: {
					username:'konglingzhan',
					pwd:'123',
				}
			}
		},
		computed:{
			username: function () {
				return this.$store.state.adminInfo
			},
			loginStatus:function() {
				return this.$store.state.loginStatus
			}
		},
		methods: {
			login () {
				if(!this.form.username){
					this.$message({
						type:'warning',
						message:'用户名不能为空'
					})
				}else if(!this.form.pwd){
					this.$message({
						type:'warning',
						message:'密码不能为空'
					})
				}else{
					login(this.form).then(res => {

						if(res.data.code == 3){
							this.$message({
								message:'用户名错误！',
								type:'warning'
							})
						}else if(res.data.code == 4){
							this.$message({
								message:'密码错误',
								type:'warning'
							})
						}else if(res.data.code == 5){
							Cookies.set('username',res.data.result)
							this.$router.push('/book')
							this.$message({
								type:'success',
								message:'登录成功'
							})
						}
					})
				}
				
			},



			register () {
				this.$router.push('/register')
			}
		},
		mounted(){

		}
	}
</script>
<style scoped>
	.login-wrap{
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background: #000;
	}
	.el-form{
		position: absolute;
		width: 400px;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
	}
	.login-title{
		color: #fff;
		font-size: 30px;
		text-align: center;
		margin-bottom: 30px;
	}
	.el-button{
		display: block;
		width: 100%;
	}
	.el-row{
		margin-bottom: 22px;
	}
	.box{
		color: #fff;
		font-size: 24px;
	}
</style>





