<template>
	<div class="login-wrap">
		<el-form ref="form" :model="form">
			<h3 class="login-title">注册帐户</h3>
			<el-form-item>
				<el-input v-model="form.username" placeholder='用户名'></el-input>
			</el-form-item>
			<el-form-item>
				<el-input type="password" v-model="form.pwd" placeholder='用户密码'></el-input>
			</el-form-item>
			<!-- <el-form-item>
				<el-input v-model="form.pwd" placeholder='确认用户密码'></el-input>
			</el-form-item> -->
			<el-row>
				<el-button type="danger" @click="register">提交注册</el-button>	
			</el-row>
			<el-row>
				<el-button type="primary" @click="backlogin">返回登陆</el-button>	
			</el-row>
		</el-form>
	</div>
</template>
<script>
import {register} from '../../api/user.js'
export default {
	data() {
		return {
			form: {}
		}
	},
	methods: {
		register () {
			register(this.form).then(res =>{
				console.log(res.data)
				if(res.data.code == 1){
					this.$router.push('/login');
					this.$message({
						type:'success',
						message:'用户注册成功！'
					})
				}else if(res.data.code == 2){
					this.$message({
						type:'warning',
						message:'用户名不能为空'
					})
				}else if(res.data.code == 3){
					this.$message({
						type:'warning',
						message:'密码不能为空'
					})
				}else if(res.data.code == 4){
					this.$message({
						type:'warning',
						message:'该用户已经存在！'
					})
				}
			})
		},
		backlogin () {
			this.$router.push('/login')
		}
	}
}
</script>
<style scoped>
.login-wrap{
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	background: #000;
}
.el-form{
	position: absolute;
	width: 400px;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
}
.login-title{
	color: #fff;
	font-size: 30px;
	text-align: center;
	margin-bottom: 30px;
}
.el-button{
	display: block;
	width: 100%;
}
.el-row{
	margin-bottom: 22px;
}
</style>


