<template>
	<div class="table-wrap" v-loading="loading">
		<el-table :data="tableData" style="width: 100%">
			<el-table-column fixed prop="name" label="书籍名" width="150"></el-table-column>
			<el-table-column prop="author" label="作者" width="120"></el-table-column>
			<el-table-column prop="borrower" label="预约者" width="120"></el-table-column>
			<el-table-column fixed="right" label="操作">
				<template slot-scope="scope">
					<el-button @click.native.prevent="book(scope.$index, tableData)" type="text" size="small">确定预约</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :pager-count="8" :page-size="15" layout="total, prev, pager, next, jumper" :total="total">
		</el-pagination>
	</div>
</template>

<script>
import {requestList,updateBook} from '../../api/book.js'
import Cookies from 'js-cookie';

export default {
	data() {
		return {
			tableData: [],
			currentPage4:1,
			total:0,
			loading:false,
		}
	},
	created () {
		this.initData({status:'3'})
	},
	methods: {
		initData (option) {
			this.loading = true;
			requestList(option).then( res => {
				this.tableData = res.data.books;
				this.total = res.data.count;

				this.loading = false;

			})
		},
		book(index, tableData) {
			var data = Object.assign({},tableData[index],{status:4});/*细究参数位置的不同最后返回的浅拷贝的结果不同*/
			updateBook(data).then(res => {
				if(res.data.code === 1){
					this.$message({
						type:'success',
						message:'确定预约'
					})
					tableData.splice(index,1)
				}
			})
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(page) {
			this.initData({status:3,page:page})
		},
	}
}
</script>
<style scoped>
.table-wrap{
	padding:20px;
}
.el-pagination{
	float: right;
	margin:20px 0;
}
</style>


