import Vue from 'vue'
import Router from 'vue-router'


import Layout from '../views/layout/layout.vue'
// 图书管理
import bookLayout from '../views/book/index.vue'
import Booking from '../views/book/booking.vue'
import BookList from '../views/book/bookList.vue'
import BookBuy from '../views/book/bookBuy.vue'
import BookAdd from '../views/book/bookAdd.vue'
import BookCenter from '../views/book/bookCenter.vue'
import BookEdit from '../views/book/bookEdit.vue'
import BookSingleCenter from '../views/book/singleCenter.vue'
import BookListForm from '../views/book/bookListFrom.vue'
import UserCenter from '../views/book/userCenter.vue'


// 公司物品管理
import goodsList from '../views/goods/goodsList.vue'
import goodsAnalyze from '../views/goods/analyze.vue'
import goodsLayout from '../views/goods/index.vue'

// 公司论坛
import blogLayout from '../views/blog/layout.vue'

// 员工OA系统
import oaLayout from '../views/oa/layout.vue'



import Login from '../views/login/index.vue'
import Register from '../views/register/register.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      redirect:'/login',
      children:[
      	{
      		path:'/book',
          component:bookLayout,
          redirect:'/book/booklist',
          children:[
            {
              path:'booking',
              component:Booking
            },
            {
              path:'booklist',
              component:BookList
            },
            {
              path:'bookbuy',
              component:BookBuy
            },
            {
              path:'bookadd',
              component:BookAdd
            },
            {
              path:'singlecenter',
              component:BookSingleCenter
            },
            {
              path:'bookcenter',
              component:BookCenter
            },
            {
              path:'bookedit',
              component:BookEdit
            },
            {
              path:'usercenter',
              component:UserCenter
            },
            {
              path:'booklistform',
              component:BookListForm
            }
          ]
      	},
        {
          path:'/goods',
          component:goodsLayout,
          redirect:'/goods/goodslist',
          children:[
            {
              path:'goodslist',
              component:goodsList
            },
            {
              path:'analyze',
              component:goodsAnalyze
            }
          ]
        },
        {
          path:'/blog',
          component:blogLayout,
        },
        {
          path:'/oa',
          component:oaLayout,
        }
      ]
    },

    {
      path:'/login',
      component:Login
    },
    {
      path:'/register',
      component:Register
    }
  ]
})



