import request from '../utils/request.js'






export function register (data) {
	return request({
		url:'/api/user/add',
		method:'post',
		data:data
	})
}
export function login (data) {
	return request({
		url:'/api/user/login',
		method:'post',
		data:data
	})
}
export function logout () {
	return request({
		url:'/api/user/logout',
		method:'post'
	})
}
export function userAddBook (data) {
	return request({
		url:'/api/user/addbookid/'+data.username,
		method:'post',
		data:data || {}
	})
}

export function userBookList (data) {
	return request({
		url:'/api/user/getuserbook/'+data.username,
		method:'post',
		data:data
	})
}
export function userList (data) {
	return request({
		url:'/api/user/find/?status='+data.status+'&username='+data.username,
		method:'get',
	})
}



