<!-- 图书列表页 -->
<template>
	<div class="main-container">
		<SideBar>
			<h2 class="nav-title">图书管理</h2>
			<router-link class="nav-item" to="/book/booklist">图书列表</router-link>
			<router-link class="nav-item" to="/book/booking">预约中心</router-link>
			<router-link class="nav-item" to="/book/bookbuy">预约订购</router-link>
			<router-link class="nav-item" to="/book/singlecenter">个人中心</router-link>
			<router-link class="nav-item" to="/book/bookcenter">图书中心</router-link>
			<router-link class="nav-item" to="/book/usercenter">用户中心</router-link>
		</SideBar>
		<div class="main-content" v-loading="loading">
			<router-view></router-view>
		</div>
	</div>
</template>
<script>
import SideBar from '../../components/Sidebar/SideBar.vue'
export default {
	data () {
		return {
			loading:false,
		}
	},
	components:{
		SideBar 
	}

}
</script>
<style scoped>
.main-container{
	position: absolute;
	top: 60px;
	bottom: 0;
	left: 0;
	right: 0;
}
.main-content{
	position: absolute;
	left: 200px;
	top: 0;
	right: 0;
	bottom: 0;
	overflow: auto;
}
.nav-item{
	display: block;
	color: #bbb;
	line-height: 35px;
	padding-left: 20px;
}
.nav-item:hover{
	background: #545454;
}
.nav-title{
	padding-left: 20px;
	color: #fff;
	font-size: 22px;
	line-height: 50px;
}
</style>


